@import "~normalize.css/normalize";
@import "fonts";
@import "global";
@import "reset";
@import "vars";

.header{
  background-color: #000;
  &--media{
    padding-bottom: 200px;
  }
  .container{
    min-height: auto;
  }
}

.mediapartners {
  background-color: #f0f0f0;
  padding: 85px 0 100px;
  &__title {
    padding-bottom: 20px;
  }
}

.about {
  margin-top: 80px;
  &__link {
    position: relative;
    display: inline-block;
    max-height: 410px;
    max-width: 730px;
    height: 100%;
    width: 100%;
    &:hover {
      .about__img {
        filter: none;
      }
    }
  }
  &__img {
    filter: grayscale(100%);
  }
  &__link-arrow {
    position: absolute;
    bottom: 30px;
    left: 0;
  }
  &__arrow-text {
    position: relative;
    display: inline-block;
    font-family: "HelveticaNeueCyr-Roman";
    font-weight: 400;
    font-size: 25px;
    line-height: 1em;
    color: #bdbdbd;
    padding-left: 100px;
    &::before {
      content: "";
      position: absolute;
      top: 50%;
      left: 0;
      height: 28px;
      width: 85px;
      transform: translateY(-50%);
      background-image: url(../images/arrow-link.svg);
      background-repeat: no-repeat;
      background-size: cover;
    }
  }
  &__link-tag {
    position: absolute;
    right: 45px;
    bottom: 0;
    display: inline-block;
    background-color: #f4fe31;
    height: 35px;
    width: 175px;
    font-family: "HelveticaNeueCyr-Bold";
    font-weight: 700;
    font-size: 18px;
    line-height: 1em;
    padding: 8px 10px;
    text-align: center;
    text-transform: uppercase;
    transform: rotate(-23.11deg);

    &::before,
    &::after {
      content: "";
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      height: 10px;
      width: 10px;
      border-radius: 50%;
      background-color: #000;
    }
    &::before {
      left: 15px;
    }
    &::after {
      right: 15px;
    }
  }
  &__info {
    display: inline-block;
    vertical-align: top;
    margin-left: 50px;
  }
  &__info-content {
    display: flex;
    flex-direction: column;
  }
  &__info-items {
    margin: 45px 0;
  }
  &__info-title {
    position: relative;
    padding-bottom: 12px;
    &::after {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      height: 2px;
      background-color: #000;
    }
  }
  &__subscribes{
    margin: 40px 0;
  }
  &__subscribe:hover{
    .subscribe__img--instagram{
      background-image: url(../images/instagram-color.png);
    }

    .subscribe__img--youtube{
      background-image: url(../images/youtube-color.png);
    }
  }
  &__descr {
    margin-top: 80px;
    flex-basis: 100%;
  }
  &__descr-title {
    font-family: "HelveticaNeueCyr-Bold";
    font-weight: 700;
    font-size: 40px;
    line-height: 1.2em;
    margin-bottom: 12px;
  }
  &__descr-text {
    padding: 0 70px 20px 15px;
    border: 2px solid #000;
  }
  &__text-inner {
    max-width: 1260px;
    padding: 20px 0;
    font-family: "HelveticaNeueCyr-Roman";
    font-weight: 400;
    font-size: 25px;
    line-height: 1.3em;
  }
}

.bloggers {
  padding: 110px 0 105px;
  &__text {
    padding: 25px 0 55px;
    font-family: "HelveticaNeueCyr-Roman";
    font-weight: 400;
    font-size: 30px;
    line-height: 1.2em;
  }
  &__card-items {
    display: flex;
    flex-wrap: wrap;
  }
}

.channels {
  padding: 60px 0 90px;
  background-color: #f0f0f0;
  &__text {
    font-family: "HelveticaNeueCyr-Roman";
    font-weight: 400;
    font-size: 30px;
    line-height: 1.3em;
    padding: 25px 0;
    max-width: 1200px;
  }
}

.channel {
  display: flex;
  max-height: 280px;
  height: 100%;
  background-color: #fff;
  box-shadow: 0px 4.59459px 9.18919px rgba(0, 0, 0, 0.07);
  margin-bottom: 40px;
  &:hover{
    .channel__img{
      filter: none;
    }
    .subscribe__img{
      background-image: url(../images/youtube-color.png);
    }
    
  }
  &__img{
    filter: grayscale(100%);
  }
  &__head{
    display: flex;
  }
  &__title{
    font-family: 'HelveticaNeueCyr-Bold';
    font-weight: 700;
    font-size: 41px;
    line-height: 1em;
    margin-right: 27px;
  }
  &__info{
    margin-left: 45px;
    padding: 30px 0 5px;
  }
  &__info--clearfix{
    display: inline-block;
  }
  &__text{
    padding: 15px 0 10px;
    font-family: 'HelveticaNeueCyr-Roman';
    font-weight: 400;
    font-size: 20px;
    line-height: 1.2em;
    max-width: 665px;
    max-height: 170px;
    overflow-y: auto; 
    scrollbar-color: #535353 #ccc;     /* «цвет ползунка» «цвет полосы скроллбара» */
	  scrollbar-width: thin;  /* ширина скроллбара*/   
  }
  &__date{
    font-family: 'HelveticaNeueCyr-Roman';
    font-weight: 400;
    font-size: 20px;
    line-height: 1.2em;
    color: #8F8F8F;
  }
}

.main{
  background-color: #f0f0f0;
  padding-top: 80px;
}

.breadcrumbs{
  margin-bottom: 60px;
  &__list{
    display: flex;
  }
  &__item{
    position: relative;
    font-family: 'HelveticaNeueCyr-Roman';
    font-weight: 400;
    font-size: 25px;
    line-height: 0.8em;
    color: rgba(#000, 0.39);
    padding-right: 24px;
    margin-right: 24px;
    &::before{
      content: '/';
      position: absolute;
      top: 0;
      right: 0;

    }
  }
  &__item:last-child::before{
    content: none;
  }
}

.hero-content{
  display: flex;
  align-items: center;
  background-color: #fff;
  padding-right: 18px;
  &__info{
    margin-left: 45px;
  }
  &__subscribes{
    display: flex;
    margin: 50px 0 60px;
  }
  &__subscribe-text{
    padding-right: 30px;
  }
  &__tags-items{
    max-width: 590px;
  }
  &__title{
    display: inline-block;
    position: relative;
    margin-bottom: 12px;
    padding-right: 20px;
  }
  &__descr{
    margin-top: 35px;
  }
  &__descr-title{
    font-size: 30px;
    margin-bottom: 12px;
  }
  &__descr-text{
    font-family: 'HelveticaNeueCyr-Roman';
    font-weight: 400;
    font-size: 20px;
    line-height: 1.3em;
    overflow-y: auto;
    max-height: 200px;
  }
  &__descr-paragraph{
    padding-bottom: 10px;
  }
}

.others-bloggers{
  padding: 100px 0 130px;
  &__cards{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    max-height: 460px;
    overflow-y: hidden;
    margin-top: 40px;
  }
  &__cards--active{
    overflow-y: visible;
    max-height: 100%;
    justify-content: flex-start;
  }
  &__card{
    background-color: #fff;
  }
  &__btn{
    display: block;
    background-color: transparent;
    width: 220px;
    height: 55px;
    font-family: 'HelveticaNeueCyr-Roman';
    font-weight: 400;
    font-size: 17px;
    line-height: 1.3em;
    text-transform: uppercase;
    border: 2px solid #000;
    cursor: pointer;
    margin: 0 auto;
  }
  &__btn--hidden{
    display: none;
  }
}

.partners{
  background-color: #000;
  overflow-x: hidden;
}

.footer{
  background-color: #000;
}

.media_block{
  padding: 1.5em 2.2em;
}